import { Component } from '@angular/core';
import { StudentInfoService } from './services/student-info.service';
import { Chaakash } from './chaakash';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

header: string;
footer: string;

studentInfo :Chaakash;

  constructor(
    private studentService: StudentInfoService
  ) {  }



  ngOnInit()
  {
    this.studentService.getLoginInfo().subscribe(data => {this.studentInfo = data;
    console.log(data);
    });
    console.log(this.studentInfo);
    
    this.header = this.studentInfo.Title + ' / ' + this.studentInfo.Name;
    this.footer= `${this.studentInfo.Name} / ${this.studentInfo.StudentNumber} / ${this.studentInfo.LoginName} / ${this.studentInfo.Campus}`
  }


  title = 'chaakash-A4';
}

